import React, {useState, useEffect} from 'react';
import {base_url, public_url, $http} from "config";
import {NotificationContainer, NotificationManager} from 'react-notifications';
import ContainerHeader from 'components/ContainerHeader/index';
import IntlMessages from 'util/IntlMessages';
import CardBox from 'components/CardBox/index';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import AttachFile from '@material-ui/icons/AttachFile';
import {useHistory} from 'react-router-dom';
import {useSelector} from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';
import Fab from '@material-ui/core/Fab';

const CustomerModify = (props) => {
    const History = useHistory();
    const customer_id = props.location.state ? props.location.state.id : null;
    const [loader, setLoader] = useState(false);
    const [name, setCompanyName] = useState("");
    const [account_manager, setAccountManager] = useState("");
    const [website, setWebsite] =  useState("");
    const [company_email, setCompanyEmail] = useState("");
    const [address, setAddress] = useState("");
    const [city, setCity] = useState("");
    const [postcode, setPostcode] = useState("");
    const [is_site, setIsSite] = useState(props.location.state ? false : true);
    const [logo_img, setLogoImg] = useState(null);
    const {width} = useSelector(({settings}) => settings);
    const [imagePreviewUrl, setLogoImgUrl] = useState('https://via.placeholder.com/300x300');

    const [contact_name, setContactName] = useState([]);

    useEffect(() => {
        $http.get(base_url + "customers/company/customer-edit", {params: {id: customer_id}})
        .then((response) => {
            if (response.data.company) {
                setCompanyName(response.data.company.name);
                setAccountManager(response.data.company.manager);
                setWebsite(response.data.company.website);
                setCompanyEmail(response.data.company.company_email);
                setAddress(response.data.company.address);
                setCity(response.data.company.city);
                setPostcode(response.data.company.postcode);
                setLogoImgUrl(response.data.company.logo_img ? 
                    (public_url + 'upload/img/' + response.data.company.logo_img) : 
                    'https://via.placeholder.com/300x300');
            }
            setLoader(true);
            setContactName(response.data.account_manager);
        })
    }, [customer_id]);

    const SaveCustomer = () => {
        
        const frmData = new FormData();
        frmData.append('id', customer_id);
        frmData.append('company_name', name);
        frmData.append('logo_img', logo_img);
        frmData.append('website', website);
        frmData.append('company_email', company_email);
        frmData.append('address', address);
        frmData.append('city', city);
        frmData.append('postcode', postcode);
        frmData.append('is_site', is_site);
        frmData.append('manager', account_manager);


        $http.post(base_url + "customers/company/customer-edit", frmData)
        .then((response) => {
            if (response.data.status === "success") {
                History.push({
                    pathname: '/app/customers/company/info',
                    state: {id: response.data.id}
                })
            } else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 5000, () => {
                    alert('callback');
                });
            }
        }).catch((error)=>{
            console.log("error:", error);
        })
    };
    const _handleImageChange = (e) =>{
        e.preventDefault();
  
        let reader = new FileReader();
        let file = e.target.files[0];
        reader.onloadend = () => {
            setLogoImg(file);
            setLogoImgUrl(reader.result);
        }
        reader.readAsDataURL(file)
    }
    return (
        <div className="app-wrapper">
            <ContainerHeader match={props.match} title={<IntlMessages id="sidebar.customers.company"/>}/>
            { loader ? (
                <div className="row">
                    <div className="col-xl-3 col-lg-4 col-md-5 col-12">
                        <div className="jr-card pb-2">
                            <div className="jr-card-thumb">
                                <img className="card-img-top img-fluid" alt="products" src={imagePreviewUrl}/>
                            </div>
                            <input type="file" id="logo_img" accept="image/*" 
                                name="logo_img" style={{display: "none"}}
                                onChange={(event) => {
                                    _handleImageChange(event);
                                }}/>

                            <div className="jr-card-social text-right">
                                <Button className="jr-btn text-primary">
                                    Company Logo
                                </Button>
                                <Fab className="jr-fab-btn bg-light-blue accent-2 text-white" onClick={(e)=>{document.getElementById('logo_img').click()}}>
                                    <i className="zmdi zmdi-cloud-upload zmdi-hc-2x"/>
                                </Fab>
                            </div>
                        </div>
                    </div>
                    <CardBox styleName="col-xl-9 col-lg-8 col-md-7 col-12">
                        <form className="row" autoComplete="off" >
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="name"
                                    label="Company Name"
                                    value={name}
                                    onChange={(event)=>setCompanyName(event.target.value)}
                                    margin="normal"
                                    helperText="Please Enter Company Name"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField
                                    id="account_manager"
                                    select
                                    label="Account Manager"
                                    value={account_manager}
                                    onChange={(event)=>setAccountManager(event.target.value)}
                                    SelectProps={{}}
                                    helperText="Please select Account Manager"
                                    margin="normal"
                                    fullWidth
                                >
                                    {contact_name.map(option => (
                                        <MenuItem key={option.id} value={option.id}>
                                            {option.first_name} {option.last_name}
                                        </MenuItem>
                                    ))}
                                </TextField>
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="website"
                                    label="Website Url"
                                    value={website}
                                    onChange={(event)=>setWebsite(event.target.value)}
                                    margin="normal"
                                    helperText="Please Enter Website"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="company_email"
                                    label="Company Email"
                                    value={company_email}
                                    onChange={(event)=>setCompanyEmail(event.target.value)}
                                    margin="normal"
                                    helperText="Please Enter Company Email"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="address"
                                    label="Address"
                                    value={address}
                                    onChange={(event)=>setAddress(event.target.value)}
                                    margin="normal"
                                    helperText="Please Enter Address"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="city"
                                    label="City"
                                    value={city}
                                    onChange={(event)=>setCity(event.target.value)}
                                    margin="normal"
                                    helperText="Please Enter City"
                                    fullWidth
                                    />
                            </div>
                            <div className="col-md-6 col-12">
                                <TextField 
                                    id="postcode"
                                    label="Postcode"
                                    value={postcode}
                                    onChange={(event)=>setPostcode(event.target.value)}
                                    margin="normal"
                                    helperText="Please Enter Postcode"
                                    fullWidth
                                    />
                            </div>
                            {
                                customer_id > 0 || (
                                    <div className="d-flex align-items-center">
                                        <Checkbox 
                                            id="is_site"
                                            color="primary"
                                            checked={is_site}
                                            onChange={(event)=>setIsSite(event.target.checked)}
                                        /> 
                                        <span>Create Site customer address</span>
                                    </div>
                                )
                            }
                            <div className="col-md-12 col-12 text-right">
                                <Button variant="contained" color="primary" className="jr-btn"
                                    onClick={()=>SaveCustomer()}
                                >
                                    Save
                                </Button>
                            </div>
                        </form>
                    </CardBox>
                </div>
            ) : (
                <div className="loader-view"
                        style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                    <CircularProgress/>
                </div>
            )}
                <NotificationContainer/>
        </div>
    );
}

export default CustomerModify;